@import url('https://fonts.googleapis.com/css2?family=Epilogue:wght@300&500&display=swap');

body {
    overflow-x: hidden;
    overflow-y: scroll;
    background-color: #0d1019;
    font-family: 'Epilogue', sans-serif;
    overflow-y: hidden;
}

.contact-us {
    width: 100%;
    height: 100%;
}

.contact-us-div {
    display: inline-flex;
    text-align: center;
}

.contact-us-div-left {
    display: inline-block;
    justify-content: center;
    padding: 1vh;
    width: 50vw;
    height: auto;
}

.contact-us-div-right {
    display: inline-block;
    justify-content: center;
    padding: 1vh;
    width: 50vw;
    height: auto;
}